<template>
    <div>
        <p>{{title}}</p>

        <input type="text" v-model="title" placeholder="Enter name">
    </div>
</template>
<script>
export default {
    data(){
        return{
            title: 'Sunny'
        }
    },
    methods:{
        display(name){
            console.log("Helloo"+name);
        }
    },
    created(){
        this.display('Twinkle');
    }
  
}
</script>
<style>

</style>



